
.vu__application-base .base-tip {
  display: inline-block;
}

.vu__application-base-manager {
  margin-top: 30px;
  border-top: 1px solid #ccc;
  padding-top: 10px;
}
.vu__application-base-manager .base-tip {
  display: inline-block;
}
.vu__application-base-manager [class^=application-base] {
  position: relative;
  margin-bottom: 10px;
  width: 50%;
  overflow: hidden;
  display: inline-block;
  border-bottom: 1px solid #ccc;
  padding: 10px;
  height: 80px;
}
.vu__application-base-manager [class^=application-base]:nth-child(2n+1) {
  border-right: 1px solid #ccc;
}
.vu__application-base-manager [class^=application-base] .base-label,
.vu__application-base-manager [class^=application-base] .base-component {
  display: inline-block;
}
.vu__application-base-manager [class^=application-base] .base-label {
  width: 100px;
  text-align: right;
}
.vu__application-base-manager [class^=application-base] .b-icon {
  position: absolute;
  top: 0;
  right: 10px;
  font-size: 18px;
  cursor: pointer;
  color: #ccc;
}
.vu__application-base-manager [class^=application-base] .b-icon:hover {
  color: #3FAAF5;
}
.vu__application-base-manager [class^=application-base] .b-icon.el-icon-edit {
  margin-right: 35px;
}
.vu__application-base-manager [class^=application-base].application-base-input-multi {
  position: relative;
}
.vu__application-base-manager [class^=application-base].application-base-input-multi .vu__base-textarea.tab {
  position: relative;
  margin-left: 100px;
}
.vu__application-base-manager [class^=application-base].application-base-input-multi .vu__base-textarea.tab.hasvalue > .base-label {
  border-color: #409EFF;
  color: #409EFF;
}
.vu__application-base-manager [class^=application-base].application-base-input-multi .vu__base-textarea.tab > .base-label {
  display: inline-block;
  width: auto;
  padding: 0 20px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  height: 40px;
  line-height: 40px;
  padding-right: 40px;
  color: #606266;
}
.vu__application-base-manager [class^=application-base].application-base-input-multi .vu__base-textarea.tab .base-textarea {
  position: absolute;
  top: 40px;
  left: 0;
  z-index: 1001;
  border: 1px solid #e4e7ed;
  border-radius: 4px;
  background-color: #fff;
  -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 5px 0;
  padding: 10px 15px;
  min-width: 300px;
}
.vu__application-base-manager [class^=application-base].application-base-input-multi .vu__base-textarea.tab .el-select__caret {
  color: #c0c4cc;
  font-size: 14px;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  -webkit-transform: rotateZ(180deg);
  -ms-transform: rotate(180deg);
  transform: rotateZ(180deg);
  line-height: 16px;
  cursor: pointer;
}
.vu__application-base-manager [class^=application-base].application-base-input-multi .vu__base-textarea.tab .el-select__caret.is-reverse {
  -webkit-transform: rotateZ(0);
  -ms-transform: rotate(0);
  transform: rotateZ(0);
}
.vu__application-base-manager .application-base-tabs > .base-label,
.vu__application-base-manager .application-base-input-multi > .base-label {
  display: none;
}
